body {
    overflow: hidden;
}
.board {
    width: 100vw;
    height: 100vh;
    background-size: 100% 100%;
    background-position: center;
    overflow: hidden;
}

.navbar {
    width: 100vw;
    height: 24px;
    backdrop-filter: blur(10vh);
    display: flex;
}

.navbar-tap {
    color: rgb(243, 243, 243);
    font-weight: 450;
    padding: 0 10px;
    text-shadow: 0 0 4px rgba(255, 255, 255, 0.25);
}

.navbar-over {
    background-color: rgba(255, 255, 255, 0.25);
    border-radius: 3px;
}

.navbar-rigth {
    margin-left: auto;
}

.dock-bar {
    position: absolute;
    bottom: 0;
    width: 600px;
    left: 50%;
    margin-left: -300px;
    backdrop-filter: blur(10vh);
    height: 75px;
    border: rgba(255, 255, 255, 0.1) 1px solid;
    border-radius: 20px;
    display: flex;
    box-sizing: border-box;
    /* animation-name: move; */
    animation-duration: 0.75s;
    /* animation-direction: normal; */
    animation-fill-mode: both;
    animation-iteration-count: 3;
    animation-play-state: running;
}
@keyframes move {
    from {
        bottom: -75px;
    }
    to {
        bottom: 0;
    }
}
.dock-icon {
    box-sizing: border-box;
    width: 64px;
    height: 64px;
    background-color: white;
    border: gray 1px solid;
    border-radius: 15px;
    margin: auto 0 auto 20px;
}

.python {
    background-size: 100% 100%;
    background-position: center;
    background-image: url("../public/img/python.png");
}

.django {
    background-size: 100% 100%;
    background-position: center;
    background-image: url("../public/img/django.png");
}

.js {
    background-size: 100% 100%;
    background-position: center;
    background-image: url("../public/img/javascript.png");
}

.finder {
    width: 1000px;
    height: 500px;

    /* display: flex;
    flex-wrap: wrap; */
}
.for-drag {
  position: absolute;
  width: 150px;
  height: 50px;
}

.exit-bar {
    box-sizing: border-box;
    width: 150px;
    height: 100%;
    backdrop-filter: blur(150px);
    float:left;
}

.drag-bar {
    box-sizing: border-box;
    width: calc(100% - 150px);
    height: 50px;
    background-color: blanchedalmond;
    float:left;
}

